.button {
    border-radius: 3px;
    padding: 5px 24px;
    /*min-width: 132px;*/
    height: 30px;
    border: none;

    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.4;
    color: #fff;
    cursor: pointer;
}


.disabled {
    background: #004d99;
    opacity: 0.5;
    color: #002e5c;
    cursor: not-allowed;
}

.secondary {
    border: 1px solid #06c;
    background-color: transparent;
    color: #06c;
}

.default {
    background: #06c;
}

.red {
    background: #cc1439;
}

.default:hover {
    background: #0080ff;
}

.red:hover {
    background: #ff1a47;
}

.secondary:hover {
    background: #e5f0fa;
}


.default:active {
    background: #0059b2;
}

.secondary:active {
    background: #c9e5ff;
}

.red:active {
    background: #b21232;
}

.button:disabled:hover {
    background: #004d99;
    opacity: 0.5;
    /*min-width: 132px;*/
}
